import Swiper from 'swiper';

document.addEventListener('DOMContentLoaded', () => {
  const sliders = document.querySelectorAll('.slider-container');
  if (sliders.length !== 0){
    [...sliders].forEach((slider) => {
      const container = slider.querySelector('.swiper-container');
      new Swiper(container, {
        slidesPerView: 1,
        loop: true,
        noSwipingClass:'banner__main',
        autoplay: true,
        pagination: {
          el: slider.querySelector('.banner__pagination'),
          clickable: true,
        },
        navigation: {
          nextEl: '.banner__next',
          prevEl: '.banner__prev',
        },
        on: {
          init: function () {
            if(container.classList.contains('banner__slider')){
              let activeSlide = container.swiper.slides[container.swiper.activeIndex];
              if (activeSlide.getAttribute('data-theme')){
                document.body.classList.add('light-theme')
              } else {
                 document.body.classList.remove('light-theme')
              }
            };
            container.swiper.update()
          },
          slideChange: function () {
            if(container.classList.contains('banner__slider')){
              let activeSlide = container.swiper.slides[container.swiper.activeIndex];
              if (activeSlide.getAttribute('data-theme')){
                document.body.classList.add('light-theme')
              } else {
                document.body.classList.remove('light-theme')
              }
            }
          },
        },
      });
    })
  }
})


