import {initBtnSettings} from "./button";
import {inputsFunc} from "./form";

export const toOrderACall = () => {
  document.querySelector('body').insertAdjacentHTML('afterbegin', `
    <div class="call-popup">
        <div class="call-popup__close">
            <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path fill-rule="evenodd" clip-rule="evenodd" d="M10.4695 12.9004L1.01525 22.3546C0.454544 22.9153 0.454543 23.8244 1.01525 24.3851C1.57596 24.9458 2.48505 24.9458 3.04576 24.3851L12.5 14.9309L21.9542 24.3851C22.5149 24.9458 23.424 24.9458 23.9847 24.3851C24.5455 23.8244 24.5455 22.9153 23.9847 22.3546L14.5305 12.9004L23.9847 3.44615C24.5455 2.88544 24.5455 1.97635 23.9847 1.41564C23.424 0.854934 22.5149 0.854934 21.9542 1.41564L12.5 10.8699L3.04576 1.41564C2.48505 0.854935 1.57596 0.854934 1.01525 1.41564C0.454544 1.97635 0.454544 2.88544 1.01525 3.44614L10.4695 12.9004Z" fill="#AB2421"/>
            </svg>
        </div>
        <h2 class="call-popup__title"> </h2>
        <p class="call-popup__text">      15 .    <span class="red-text"> 7:00  16:00 (  )</span></p>
         <form class="form">
           <div class="form__item call-popup__item">
             <input class="form__input call-popup__input" type="text" id="call-popup-name" name="name"/>
             <label class="form__input-label" for="call-popup-name">  </label>
           </div>
           <div class="form__item call-popup__item">
             <input class="form__input call-popup__input" type="text" data-mask="phone" id="call-popup-phone" name="phone"/>
             <label class="form__input-label" for="call-popup-phone"> </label>
           </div>
           <div class="form__item call-popup__item">
             <input class="form__input call-popup__input" type="text" id="call-popup-email" name="email"/>
             <label class="form__input-label" for="call-popup-email">  e-mail</label>
           </div>
           <button class="btn btn__red form__btn call-popup__btn" type="submit"></button>
           <div class="call-popup__comments">
             <div>,    -       </div>
             <div>    ,     <a class="form__personal-data" href="#">  </a></div>
           </div>

         </form>
    </div>
`);
  let toCallPopup = document.querySelector('.call-popup')
  setTimeout(() => {
    toCallPopup.classList.add('call-popup__opened');
    toCallPopup.insertAdjacentHTML('afterend',`<div class="dark-block"></div>`);
    document.body.style.overflow = "hidden"
  },100)

  initBtnSettings(toCallPopup);
  inputsFunc(toCallPopup)

  let btnClose = toCallPopup.querySelector('.call-popup__close');
  let closePopupCall = () => {
    toCallPopup.classList.add('call-popup__closed')
    setTimeout(() => {
      toCallPopup.remove();
      document.querySelector('.dark-block').remove();
      document.body.style.overflow = "auto"
    },100)
  }

  btnClose.addEventListener('click',() => {
    closePopupCall()
  })

  document.addEventListener('click', (e) => {
    if (e.target === document.querySelector('.dark-block')){
      closePopupCall()
    }
  })



  let form = toCallPopup.querySelector('.form');
  form.addEventListener('submit', () => {
    let formInputsLength = toCallPopup.querySelectorAll('.form__item')?.length;
    let formInputsSuccessLength = toCallPopup.querySelectorAll('.form__input-success')?.length;
    if (formInputsLength === formInputsSuccessLength){
      closePopupCall()
    }
  })
}
let toCallBtns = document.querySelectorAll('.btn__header');
toCallBtns.forEach(el => {
  el.addEventListener('click', toOrderACall)
})









