import Form from './_validate'
import {addErrorMessage} from "./error-message";
import {initBtnSettings} from "./button";

export const inputsFunc = (element) => {
  const form = new Form(element.querySelectorAll('.form'), {
    focusValidate: true,
    classes: {
      empty: '.empty',
      error: 'form__input-error',
      correct: 'form__input-success',
    },
    fields: [
      {
        selector: '[name="name"]',
        maxLength: 32,
        realTimeRegExp: 'name',
        realTime: true,
        required: true
      },
      {
        selector: '[name="phone"]',
        realTimeRegExp: 'phone',
        realTime: true,
        required: true,
        minLength: 12,
        mask: '+7**********'
      },
      {
        selector: '[name="email"]',
        regExp: 'email',
        realTime: true,
        required: true,
      },
    ]
  });

  form.on('submit', function(e) {
    e.preventDefault();

    document.querySelector('body').insertAdjacentHTML('afterbegin', `
    <div class="success-popup">
        <button class="success-popup__close">
                <svg width="48" height="49" viewBox="0 0 48 49" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M19.939 24.8008L1.0305 43.7093C-0.0909117 44.8307 -0.090914 46.6489 1.0305 47.7703V47.7703C2.15192 48.8917 3.97009 48.8917 5.09151 47.7703L24 28.8618L42.9085 47.7703C44.0299 48.8917 45.8481 48.8917 46.9695 47.7703V47.7703C48.0909 46.6489 48.0909 44.8307 46.9695 43.7093L28.061 24.8008L46.9695 5.89229C48.0909 4.77088 48.0909 2.9527 46.9695 1.83128V1.83128C45.8481 0.709867 44.0299 0.709868 42.9085 1.83128L24 20.7398L5.09151 1.83129C3.97009 0.70987 2.15192 0.709868 1.0305 1.83128V1.83128C-0.0909129 2.9527 -0.090913 4.77087 1.0305 5.89229L19.939 24.8008Z" fill="white"/>
                </svg>
        </button>
        <img class="success-popup__img" src="images/form-success.svg" alt="success">
        <h2 class="success-popup__title">   !</h2>
        <p class="success-popup__text">    ,  - </p>
        <button class="btn btn__red success-popup__btn">Ok</button>
    </div>
`)


    let successPopup = document.querySelector('.success-popup');
    successPopup.insertAdjacentHTML('afterend',`<div class="dark-block"></div>`);
    document.body.style.overflow = "hidden"
    let closeBtn = document.querySelector('.success-popup__close');

    let closePoupup = () => {
      successPopup.remove();
      document.querySelector('.dark-block').remove();
      document.body.style.overflow = "auto"
      element.querySelectorAll('.form__input').forEach(el => {
        el.value = "";
        el.parentElement.classList.remove('form__input-success');
        el.parentElement.querySelector('.form__input-label').classList.remove('form__input-checked');
      })
    }
    if (closeBtn){
      closeBtn.addEventListener('click', () => {
      closePoupup()
      })
    }

    document.addEventListener('click', (e) => {
      if (e.target === document.querySelector('.dark-block')){
        closePoupup()
      }
    })

    let successBtn = document.querySelector('.success-popup__btn');
    if (successBtn){
      successBtn.addEventListener('click',() => {
        successPopup.remove();
        document.body.style.overflow = "auto"
        document.querySelector('.dark-block').remove();
        document.querySelectorAll('.form__input').forEach(el => {
          el.value = "";
          el.parentElement.classList?.remove('form__input-success');
          el.parentElement.querySelector('.form__input-label').classList.remove('form__input-checked');
        })
      })
    }

    initBtnSettings(successPopup)
  })

  let inputs = element.querySelectorAll('.form__input');
  inputs.forEach(el => {
    if (el.value && el.value !== '+7'){
      el.parentElement.querySelector('.form__input-label').classList.add('form__input-checked')
    } else
      if (el.name === 'phone'){
        el.value=""
      } else {
      el.value=""
      el.parentElement.querySelector('.form__input-label').classList.remove('form__input-checked')
    }

      el.addEventListener('blur', () => {
        if (el.value){
          el.parentElement.querySelector('.form__input-label').classList.add('form__input-checked')
        } else
        if (el.name === 'phone'){
          el.value="+7"
        } else {
          el.value=""
          el.parentElement.querySelector('.form__input-label').classList.remove('form__input-checked')
        }
      })

    el.addEventListener('blur', () => {
      if (el.parentElement.classList.contains('form__input-error')){
        el.parentElement.classList.add('form__item-error');
        if(el.name === 'name'){
          el.insertAdjacentHTML('afterend','<div class="form__error-comment">  :  ,  - </div>')
        } else if (el.name === 'phone') {
          el.insertAdjacentHTML('afterend','<div class="form__error-comment">  :     11 </div>')
        } else if (el.name === 'email'){
          el.insertAdjacentHTML('afterend','<div class="form__error-comment">  ,     : mail@mail.ru</div>')
        }
      }
      else if (el.parentElement.classList.contains('form__input-success')){
        el.parentElement?.classList?.remove('form__item-error')

        if (el.parentElement.querySelector('.form__error-comment')){
          el.parentElement.querySelector('.form__error-comment').remove()
        }
      }
      else {
        el.value = "";
        el.parentElement.querySelector('.form__input-label').classList.remove('form__input-checked')
        if (el.parentElement.classList.contains('form__item-error')){
          el.parentElement.classList.remove('form__item-error')
        }
      }

      if (el.parentElement.querySelector('.form__error-comment')){
        addErrorMessage()
      }
    })

    el.addEventListener('input', () => {
      if (el.value){
        el.parentElement.querySelector('.form__input-label').classList.add('form__input-checked');
        if (el.parentElement.querySelector('.form__error-comment')){
          el.parentElement.querySelector('.form__error-comment').remove()
        }
      } else {
        el.parentElement.querySelector('.form__input-label').classList.remove('form__input-checked')
        el.parentElement.classList.remove('form__item-error')
        if (el.parentElement.querySelector('.form__error-comment')){
          el.parentElement.querySelector('.form__error-comment').remove()
        }
      }
    })
  })
}

window.inputsFuncGlobal = (el) => {
  inputsFunc(el)
}

document.addEventListener('DOMContentLoaded', () => inputsFunc(document.querySelector('.contacts')));
document.addEventListener('DOMContentLoaded', () => inputsFunc(document.querySelector('.profy')));
