import Swiper from "swiper";

const addCursorBlock = (e) => {
  if (!document.querySelector('.cursor-block')) {
    document.querySelector('body').insertAdjacentHTML('afterbegin', `<div class="cursor-block"></div>`)
  } else {
    let div = document.querySelector('.cursor-block')
    div.style.left = (e.clientX - 40) + 'px'
    div.style.top = (e.clientY - 40 + window.scrollY) + 'px';
  }

}
let sliders= document.querySelectorAll('.slide-js');
if (sliders){
  sliders.forEach(el => {
    el.addEventListener('click', () => {
      document.body.style.overflow = "hidden";
      el.querySelector('iframe').style.opacity = '1';
      let activeIndex = el.closest('.swiper-container').swiper.activeIndex;
      document.querySelector('body').insertAdjacentHTML('afterbegin', `<div class="slider-popup">${el.closest('.swiper-container').outerHTML}</div>`)
      let sliderPopup = document.querySelector('.slider-popup')


      document.querySelector('body').insertAdjacentHTML('afterbegin', `<div class="cursor-block"></div>`)
      sliderPopup.addEventListener('mousemove', (e) => {
        if (!sliderPopup.querySelector('.swiper-slide-active')?.classList.contains('slide-video-js')){
            addCursorBlock(e)
        } else {
          document.querySelector('.cursor-block')?.remove()
        }

      })

      sliderPopup.insertAdjacentHTML('afterbegin',
        `<button class="slider-popup__close">
                <svg width="48" height="49" viewBox="0 0 48 49" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path fill-rule="evenodd" clip-rule="evenodd" d="M19.939 24.8008L1.0305 43.7093C-0.0909117 44.8307 -0.090914 46.6489 1.0305 47.7703V47.7703C2.15192 48.8917 3.97009 48.8917 5.09151 47.7703L24 28.8618L42.9085 47.7703C44.0299 48.8917 45.8481 48.8917 46.9695 47.7703V47.7703C48.0909 46.6489 48.0909 44.8307 46.9695 43.7093L28.061 24.8008L46.9695 5.89229C48.0909 4.77088 48.0909 2.9527 46.9695 1.83128V1.83128C45.8481 0.709867 44.0299 0.709868 42.9085 1.83128L24 20.7398L5.09151 1.83129C3.97009 0.70987 2.15192 0.709868 1.0305 1.83128V1.83128C-0.0909129 2.9527 -0.090913 4.77087 1.0305 5.89229L19.939 24.8008Z" fill="white"/>
                </svg>
            </button>`
      )
      sliderPopup.insertAdjacentHTML('beforeend', `
        <div class="slider-popup__pagination">
            <img class="slider-popup__pagination-img" src="/local/templates/impservice/images/galery.svg" alt="galery"/>
            <div class="slider-popup__pagination-data">
                <span class="slider-popup__active-index">${activeIndex + 1}</span> / ${document.querySelector('.slider-popup').querySelectorAll('.swiper-slide').length}
          </div>
        </div>`)

      sliderPopup.querySelector('.swiper-container')?.classList.add('slider-popup__slider')
      sliderPopup.querySelector('.swiper-container').style.width = '100%';
      sliderPopup.querySelector('.swiper-container').style.height = '100%';
      sliderPopup.querySelectorAll('.swiper-slide')?.forEach(el => {
        el.style.width = '100%';
      })

      let closeBtn = document.querySelector('.slider-popup__close');
      let cursorBlock = document.querySelector('.cursor-block');
      if (closeBtn){
        closeBtn.addEventListener('click', () => {
          document.querySelector('.slider-popup').remove()
          cursorBlock.remove()
          document.body.style.overflow = "auto";
          el.querySelector('iframe').style.opacity = '0';
        })
      }

      let hiddenArray = [
          ...sliderPopup.querySelectorAll('.slider-popup__close'),
          ...sliderPopup.querySelectorAll('.slide-data'),
          ...sliderPopup.querySelectorAll('.navigation'),
          ...sliderPopup.querySelectorAll('.slider-popup__pagination'),
          ...sliderPopup.querySelectorAll('.slide-play')
      ];

      hiddenArray.forEach(el => {
        el.addEventListener('mouseover', () => {
          document.querySelectorAll('.cursor-block').forEach(el => {
            el.style.display = "none"
          })
        })
        el.addEventListener('mouseout', () => {
          document.querySelectorAll('.cursor-block').forEach(el => {
            el.style.display = "block"
          })
        })
      })

      const swiper = new Swiper('.slider-popup__slider', {
        slidesPerView: 1,
        initialSlide: activeIndex,
        allowTouchMove: false,
        navigation: {
          nextEl: '.banner__next',
          prevEl: '.banner__prev',
        },
        zoom: {
          maxRatio: 3,
        },
      });


      swiper.on('click', function (e) {
        if (!e.target.closest('.swiper-zoom-container')) return
        let zoomImg = sliderPopup.querySelector('.swiper-slide-zoomed');

        if (sliderPopup.querySelector('.swiper-slide-active')?.classList.contains('slide-video-js')) {
          swiper.zoom.disable();
        } else if (!zoomImg){
          swiper.zoom.enable()
          swiper.zoom.in();
          if (document.querySelector('.cursor-block')){
            document.querySelector('.cursor-block').style.display = "none"
         }

        } else {
          swiper.zoom.out();
          if (document.querySelector('.cursor-block')){
            document.querySelector('.cursor-block').style.display = "block"
          }
        }
      })
      swiper.on('slideChange', function () {
        sliderPopup.querySelector('.slider-popup__active-index').innerText = `${swiper.activeIndex + 1}`;
        swiper.zoom.disable();
        swiper.zoom.out();

      });
      const sliderPlayBtns = sliderPopup.querySelectorAll('.slide-play');

      sliderPlayBtns.forEach(btn => {
          btn.addEventListener('click', () => {
            btn.classList.add('hidden');
            if (btn.parentElement.querySelector('.slide-preview-wrp')){
              btn.parentElement.querySelector('.slide-preview-wrp').style.display = "none"
            }

            btn.parentElement.querySelector('video')?.play()
            btn.parentElement.querySelector('video')?.setAttribute('controls','controls')
            sliderPopup.classList.add('slider__video-played')
            let oldSrc = btn.parentElement.querySelector('iframe')?.getAttribute('src');
            let newSrc = oldSrc;
            if (oldSrc.indexOf('autoplay') !== -1){
              newSrc = oldSrc.replace("autoplay=0","autoplay=1")
            } else {
              if (oldSrc.indexOf('?') == -1){
                newSrc = newSrc + "?";// 
              }
            else
                newSrc = newSrc + "&";// 
              newSrc = newSrc + "autoplay=1";// 
            }

            btn.parentElement.querySelector('iframe')?.setAttribute('src', newSrc);
            btn.parentElement.querySelector('.slide-preview')?.classList.add('hidden');
          })
      })

      //    
      let popupNavigationBtns = sliderPopup.querySelectorAll('.navigation__btn');//   -
      popupNavigationBtns?.forEach(btn => {
        btn.addEventListener('click', () => {
          sliderPopup.querySelectorAll('iframe')?.forEach(el => {
            let oldSrc = el?.getAttribute('src');
            let newSrc = oldSrc.replace("autoplay=1","autoplay=0")
            el?.setAttribute('src', newSrc);
            if (btn.parentElement.querySelector('.slide-preview-wrp')){
              btn.parentElement.querySelector('.slide-preview-wrp').style.display = "block"
            }
            el.parentElement.querySelector('.slide-play').classList.remove('hidden')
            el.parentElement.querySelector('.slide-preview').classList.remove('hidden')
            sliderPopup?.classList.remove('slider__video-played')
          })
          sliderPopup.querySelectorAll('video').forEach(el => {
            el.parentElement.querySelector('.slide-play')?.classList.remove('hidden')
            el?.removeAttribute('autoplay');
            el?.removeAttribute('controls');
            el?.pause();
            el.currentTime = 0
          })
          })
      })

      let line = sliderPopup.querySelector('.slide-data');
      let btns = sliderPopup.querySelector('.navigation');
      let pagination = sliderPopup.querySelector('.slider-popup__pagination');
      [line, btns, pagination].forEach(el => {
        if (el){
          el.addEventListener('mouseover', () => {
            sliderPopup.classList.add('slider__mouseover')
          })
          el.addEventListener('mouseout', () => {
            sliderPopup.classList.remove('slider__mouseover')
          })
        }
      })
    })
  })
}







